            hmarketing: false,
            hbanner: false,
            hdecor: false,
            hGifts: false,
            hIndustry: false,

            slogo: {display: 'none'},
            shome: {display: 'inline-block'},

            services:[{val:'Design Services',link:'facebook.com'},{val:'Every Door Direct Mail ®',link:'#'},{val:'Logo Design Services',link:'#'},{val:'Marketing Services',link:'#'},{val:'Design Services',link:'#'}],
