        }
    },
    components: {
      
    },
    methods: {
        mohall: function(){this.hall = true},
        mlhall: function(){this.hall = false},
        mocard: function(){this.hcard = true},
        mlcard: function(){this.hcard = false},
        momarketing: function(){this.hmarketing = true},
        mlmarketing: function(){this.hmarketing = false },
        mobanner: function(){this.hbanner = true},
        mlbanner: function(){this.hbanner = false},
        modecor: function(){this.hdecor = true},
        mldecor: function(){this.hdecor = false},
        mogifts: function(){this.hGifts = true},
        mlgifts: function(){this.hGifts = false},
        moindustry: function(){this.hIndustry = true},
        mlindustry: function(){this.hIndustry = false},

        handleSCroll (event) {
            let header = document.querySelector("#product-menu-wrapper");
            if (window.scrollY > 200 ) {
                header.classList.add('sticky-menu');
                this.shome.display = 'none';
                this.slogo.display = 'inline-block';
            }else{
                header.classList.remove('sticky-menu');
                this.shome.display = 'inline-block';
                this.slogo.display = 'none';
            }
        }


    },
    created () {
      window.addEventListener('scroll', this.handleSCroll);

    } 
}
</script>


