<script>
import {Slide} from 'vue-burger-menu'
export default {
    name: 'THeader',
    data() {
        return{
            image: require('@/assets/images/logo_no_scroll.svg'),
            navView: false,
            sidebarallproducts: false,
            headertext: '',
            logo: '',
            allproductsShow: false,
            businesscardsShow: false,
            marketShow: false,
            bannerShow:false,
            decorShow: false,
            gpShow: false,
            industryShow: false,

            shapes: [{val:'Standard',link:'',new:false},{val:'Square',link:'',new:false},{val:'Circle',link:'',new:false},{val:'Oval',link:'',new:false},{val:'Leaf',link:'',new:false},{val:'Slim',link:'',new:false},{val:'Half-Circle',link:'',new:false},{val:'Folded',link:'',new:false}],
            stocks:[{val:'Glossy',link:'',new:false},{val:'Uncoated',link:'',new:false},{val:'Matte',link:'',new:false},{val:'Recycled',link:'',new:false},{val:'Magnet',link:'',new:true},{val:'Triple - Layered Trifecta',link:'',new:false}
                        ,{val:'Black',link:'',new:false},{val:'Red',link:'',new:false},{val:'Pearl',link:'',new:false},{val:'Green',link:'',new:false},{val:'Blue',link:'',new:true}],
            market1: [{val:'Bookmarks',link:'',new:false},{val:'Brochures',link:'',new:true},{val:'Business Card Magnets',link:'',new:false},{val:'RCD Packages',link:'',new:false},{val:'Club Flyers',link:'',new:false},{val:'Collectors Cards',link:'',new:false},{val:'Door Hangers',link:'',new:false},{val:'Event Tickets',link:'',new:false},{val:'Flyers',link:'',new:false},{val:'Folded Hang Tags',link:'',new:false},{val:'Folders',link:'',new:false}],
            market2: [{val:'Hang Tags',link:'',new:false},{val:'Postcards',link:'',new:true},{val:'Postcard Magnets',link:'',new:false},{val:'Rack Cards',link:'',new:false},{val:'Rip Cards',link:'',new:false},{val:'Roll Labels',link:'',new:false},{val:'Stickers',link:'',new:false},{val:'Table Tents',link:'',new:false}],
            stationaries: [{val:'Greeting Cards',link:'',new:false},{val:'Envelopes',link:'',new:false},{val:'Letterheads',link:'',new:false},{val:'Notepads',link:'',new:false}],
            decors: [{dval:'Canvas Prints',dlink:'',new:true},{dval:'Framed Prints',dlink:'',new:false},{dval:'Metal Wall Prints',dlink:'',new:false},{dval:'Mounted Wall Prints',dlink:'',new:false},{dval:'Photo Plaque',dlink:'',new:false}],
            promos: [{val:'Mouse Pads',link:'',new:false},{val:'Mugs',link:'',new:false},{val:'Photo Books',link:'',new:true},{val:'Photo Gifts',link:'',new:false},{val:'Puzzles',link:'',new:false}],
            industries:[{val:'Photography',link:''},{val:'Political',link:''},{val:'Real Estate',link:''},{val:'Religious',link:''},{val:'Restaurant/Cafe',link:''}
                        ,{val:'Retail',link:''},{val:'Sports',link:''},{val:'Trade Show',link:''},{val:'Wedding',link:''}],

        }
    },
    components:{
        Slide
    },
    mounted(){

    },
    methods:{
        
        onNav(event) {
            
            var sbar = document.querySelector("#sidebar");
            
            if (sbar.classList.contains('active')) {
                sbar.classList.remove('active');
                this.navView = false;
            }else{
                sbar.classList.add('active');
                this.navView = true;
            }
        },
        onProducts(num){
            var sbar = document.querySelector("#sidebar");
            let all = document.querySelector("#sideBarAllProducts");
            let htext = document.querySelector(".sidebar-heading");
            let comp = document.querySelector(".components");
            let logo = document.querySelector(".sidebar-header .logo_scroll");
            switch (num) {
                case 0:
                    if (sbar.classList.contains('active')) {
                        sbar.classList.remove('active');
                        this.navView = false;
                        // this.show();
                        // all.style.display = 'none';
                        htext.style.display = 'none';
                        logo.style.display = 'block';
                        setTimeout(() => all.style.display = 'none', 200);
                        setTimeout(() => comp.style.display = 'block', 200);
                    }else{
                        sbar.classList.add('active');
                        this.navView = true;
                    }
                    break;
                case 1:
                    this.show();
                    setTimeout(() => comp.style.display = 'none', 200);
                    this.allproductsShow = true;
                    all.style.display = 'block';
                    htext.style.display = 'block';
                    logo.style.display = 'none';
                    this.headertext = 'All Products';
                    break;

                case 2:
                    
                    this.show();
                    setTimeout(() => comp.style.display = 'none', 200);
                    this.businesscardsShow = true;
                    all.style.display = 'block';
                    htext.style.display = 'block';
                    logo.style.display = 'none';
                    // comp.style.display = 'none';
                    this.headertext = 'Business Cards';
                    break;

                case 3:
                    this.show();
                    this.marketShow = true;
                    all.style.display = 'block';
                    htext.style.display = 'block';
                    logo.style.display = 'none';
                    comp.style.display = 'none';
                    this.headertext = 'Marketing & Stationary';
                    break;

                case 4:
                    this.show();
                    this.bannerShow = true;
                    all.style.display = 'block';
                    htext.style.display = 'block';
                    logo.style.display = 'none';
                    comp.style.display = 'none';
                    this.headertext = 'Signs & Banner';
                    break;

                case 5:
                    this.show();
                    this.decorShow = true;
                    all.style.display = 'block';
                    htext.style.display = 'block';
                    logo.style.display = 'none';
                    comp.style.display = 'none';
                    this.headertext = 'Home Decor';
                    break;

                case 6:
                    this.show();
                    this.gpShow = true;
                    all.style.display = 'block';
                    htext.style.display = 'block';
                    logo.style.display = 'none';
                    comp.style.display = 'none';
                    this.headertext = 'Gift & Promo';
                    break;

                case 7:
                    this.show();
                    this.industryShow = true;
                    all.style.display = 'block';
                    htext.style.display = 'block';
                    logo.style.display = 'none';
                    comp.style.display = 'none';
                    this.headertext = 'Industry';
                    break;
            
                default:
                    var sbar = document.querySelector("#sidebar");
                    this.navView = true;
                    sbar.classList.remove('active');
                    setTimeout(() => sbar.classList.add('active'), 200);;
                    setTimeout(() => all.style.display = 'none', 200);
                    setTimeout(() => htext.style.display = 'none', 200);
                    setTimeout(() => logo.style.display = 'block', 200);
                    setTimeout(() => comp.style.display = 'block', 200);
                    break;
            }
            
        },
        onNavClose() {
            setTimeout(() => this.navView = false, 500);
        },
        show(){
            var sbar = document.querySelector("#sidebar");
            this.navView = true;
            sbar.classList.remove('active');
            setTimeout(() => sbar.classList.add('active'), 200);
            this.allproductsShow = false;
            this.businesscardsShow = false;
            this.marketShow = false;
            this.bannerShow = false;
            this.decorShow = false;
            this.gpShow = false;
            // setTimeout(() => this.industryShow = false, 200);
            this.industryShow = false;
        },
        // displaytask: function(task){
        //     this.allproductsShow = true;
        // }
    }
}
</script>

