}

.new-badge:after {
    color: #8cc63f;
}

.new-badge-right .new_text {
    position: absolute;
    font-size: 0.6rem;
    right: 0.4rem !important;
    /* margin-top: -0.0rem; */
}
.new-badge-right .new_text {
    color: #fff !important;
}
</style>



