  mounted() {
    this.comactive = document.querySelector("ul.companyactive");
    this.csactive = document.querySelector("ul.csactive");
    this.resactive = document.querySelector("ul.resactive");
    this.seractive = document.querySelector("ul.seractive");

    this.comarrow = document.querySelector("footer .container .comarrow i");
    this.csarrow = document.querySelector("footer .container .csarrow i");
    this.resarrow = document.querySelector("footer .container .resarrow i");
    this.serarrow = document.querySelector("footer .container .serarrow i");
    this.initHeight();
  },
  methods: {
    onSlideStart(slide) {
      this.sliding = true;
    },
    onSlideEnd(slide) {
      this.sliding = false;
    },
    onReadMore: function() {
      let more = document.querySelector(".read-more-cyber");
      let moredata = document.querySelector(".holidayAccordion");
      let tp = document.querySelector(".transparent-class");
      if (more.classList.contains("active")) {
        more.classList.remove("active");
        this.readless.display = "none";
        this.moretext = "Read More";
        this.arrowicon = "fa fa-angle-down";
        tp.style.opacity = "0.5";
        tp.style.height = "2rem";
        this.showoptions = false
      } else {
        more.classList.add("active");
        this.readless.display = "block";
        this.moretext = "Read Less";
        this.arrowicon = "fa fa-angle-up";
        this.showoptions = true
        tp.style.opacity = 1;
        tp.style.height = "auto";
      }
    },
    initHeight: function(){

        this.$refs['moreDetails'].style.height = 'auto';

        const height = getComputedStyle(this.$refs['moreDetails']).height;      
        this.computedHeight = height;  

        this.$refs['moreDetails'].style.height = 0;

      },

    onFooter() {
      this.csactive.style.display = "none";
      this.resactive.style.display = "none";
      this.seractive.style.display = "none";
      if (this.comactive.style.display == "none") {
        this.onarrowdefault();
        this.comarrow.classList.remove("fa-angle-right");
        this.comarrow.classList.add("fa-angle-down");
        this.comactive.style.display = "block";
      } else {
        this.comarrow.classList.add("fa-angle-right");
        this.comarrow.classList.remove("fa-angle-down");
        this.comactive.style.display = "none";
      }
    },
    onFooter1() {
      this.comactive.style.display = "none";
      this.resactive.style.display = "none";
      this.seractive.style.display = "none";
      if (this.csactive.style.display == "none") {
        this.onarrowdefault();
        this.csarrow.classList.remove("fa-angle-right");
        this.csarrow.classList.add("fa-angle-down");
        this.csactive.style.display = "block";
      } else {
        this.csarrow.classList.add("fa-angle-right");
        this.csarrow.classList.remove("fa-angle-down");
        this.csactive.style.display = "none";
      }
    },
    onDefaultunstyled() {},
    onarrowdefault() {
      this.comarrow.classList.remove("fa-angle-down");
      this.csarrow.classList.remove("fa-angle-down");
      this.resarrow.classList.remove("fa-angle-down");
      this.serarrow.classList.remove("fa-angle-down");
      this.comarrow.classList.add("fa-angle-right");
      this.csarrow.classList.add("fa-angle-right");
      this.resarrow.classList.add("fa-angle-right");
      this.serarrow.classList.add("fa-angle-right");
    }
  }
};
</script>
