  top: auto !important;
  left: 0;
  margin: 0;
  padding: 0.4rem 0.1rem;
  text-align: center;
}

.cds-texttablet h2,
.cds-texttablet h3 {
  margin: 0;
  text-align: center;
}
.cds-texttablet h2 {
  line-height: 3rem;
  font-weight: bold;
  font-size: 1.8rem;
}
.cds-texttablet h3,
.cds-texttablet p {
  font-size: 1.2rem;
  line-height: 1rem;
  font-weight: 300;
}
.cds-texttablet p {
  display: none;
}
.cds-texttablet .button {
  margin-top: 1rem;
}

.guarantee-block {
  margin: 5rem 0 5rem 0rem;
}
.content-fixed .col-sm-4,
.content-fixed .col-sm-8 {
  padding-left: 1rem;
  padding-right: 1rem;
}
.content-fixed .col-sm-4 {
  margin-bottom: 2rem;
}

.content-fixed img {
  border-radius: 0.5rem;
}
.promotional-svg {
  height: 3.5rem;
  margin-right: 2rem;
  /* margin-top: -1rem; */
}
.fl {
  float: left;
}
.col-sm-6 p {
  font-size: 0.9rem;
  line-height: 1.3rem;
  text-align: justify;
}
.col-sm-6 h4 {
  font-size: 1.2rem;
  font-weight: bold;
  color: #484848;
  margin-bottom: 1.0rem;
  line-height: 2.6rem;
}
.para {
  font-size: 0.85rem;
  padding: 1rem 0;
  line-height: 1.6rem;
}
.transparent-class {
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
  filter: alpha(opacity=50);
  -moz-opacity: 0.5;
  -khtml-opacity: 0.5;
  opacity: 0.5;
  height: 2rem;
  overflow: hidden;
  display: block;
}

.holidayAccordion{
    height: 0;
    overflow: hidden;
    transition: 0.5s;
}

</style>



