<script>
import { mapGetters } from "vuex";
export default {
  name: "Footer",
  data() {
    return {
      companys: ["About Us", "Locations", "Our Equipment", "Careers", "Blog"],
      cservices: [
        "Guarantee",
        "Report an Order Problem",
        "Production Time",
        "Feedback",
        "Testimonials",
        "Coupons"
      ],
      resources: [
        "Pricing",
        "Paper Stocks",
        "Ways to Order",
        "Preparing Files",
        "Download Templates",
        "View Samples"
      ],
      services: [
        "Print Brokers",
        "Design Services",
        "Logo Design Services",
        "EDDM® Services",
        "Mailing Services",
        "Direct Marketing Packages",
        "Request Sample Kit"
      ]
    };
  },
  methods: {
    handleResize(event) {
      if (window.innerWidth <= 767) {
        this.onarrowdefault();
        this.comactive.style.display = "none";
        this.csactive.style.display = "none";
        this.resactive.style.display = "none";
        this.seractive.style.display = "none";
      } else {
        this.comactive.style.display = "block";
        this.csactive.style.display = "block";
        this.resactive.style.display = "block";
        this.seractive.style.display = "block";
      }
    },
    onFooterClick(num) {
      switch (num) {
        case 1:
          this.onFooter();
          break;
        case 2:
          this.onFooter1();
          break;

        default:
          break;
      }
    },
    onFooter() {
      this.csactive.style.display = "none";
      this.resactive.style.display = "none";
      this.seractive.style.display = "none";
      if (this.comactive.style.display == "none") {
        this.onarrowdefault();
        this.comarrow.classList.remove("fa-angle-right");
        this.comarrow.classList.add("fa-angle-down");
        this.comactive.style.display = "block";
      } else {
        this.comarrow.classList.add("fa-angle-right");
        this.comarrow.classList.remove("fa-angle-down");
        this.comactive.style.display = "none";
      }
    },
    onFooter1() {
      this.comactive.style.display = "none";
      this.resactive.style.display = "none";
      this.seractive.style.display = "none";
      if (this.csactive.style.display == "none") {
        this.onarrowdefault();
        this.csarrow.classList.remove("fa-angle-right");
        this.csarrow.classList.add("fa-angle-down");
        this.csactive.style.display = "block";
      } else {
        this.csarrow.classList.add("fa-angle-right");
        this.csarrow.classList.remove("fa-angle-down");
        this.csactive.style.display = "none";
      }
    },
    onFooter2() {
      this.comactive.style.display = "none";
      this.csactive.style.display = "none";
      this.seractive.style.display = "none";
      if (this.resactive.style.display == "none") {
        this.onarrowdefault();
        this.resarrow.classList.remove("fa-angle-right");
        this.resarrow.classList.add("fa-angle-down");
        this.resactive.style.display = "block";
      } else {
        this.resarrow.classList.add("fa-angle-right");
        this.resarrow.classList.remove("fa-angle-down");
        this.resactive.style.display = "none";
      }
    },
    onFooter3() {
      this.comactive.style.display = "none";
      this.csactive.style.display = "none";
      this.resactive.style.display = "none";

      if (this.seractive.style.display == "none") {
        this.onarrowdefault();
        this.serarrow.classList.remove("fa-angle-right");
        this.serarrow.classList.add("fa-angle-down");
        this.seractive.style.display = "block";
      } else {
        this.serarrow.classList.add("fa-angle-right");
        this.serarrow.classList.remove("fa-angle-down");
        this.seractive.style.display = "none";
      }
    }
  },
  computed: {
    ...mapGetters(["device"]),
    isMobile() {
      return this.device === "mobile";
    }
  },
  mounted() {
    //window.addEventListener("resize", this.handleResize);
  },
  beforeDestroy() {
    //window.removeEventListener("resize", this.handleResize);
  }
};
</script>

<style scoped >
#blogIcon svg {
  position: absolute;
  /* top: 0.3rem; */
  max-height: 2rem;
  left: 0;
}

/* #foooter #eco-icon svg .st0, #footer #eco-icon svg .st0 {
    fill: #ffffff;
} */

#customer-icon {
  position: absolute;
  left: 1rem;
  top: 0;
  color: #fff;
  fill: #fff;
  width: 10rem;
}

#eco-icon {
  position: absolute;
  right: 0.1rem;
  top: 0;
  fill: #ffffff;
}
#eco-icon:hover {
  fill: #8cc63f;
}
#customer-icon .fas {
  font-size: 1.9rem;
}
#eco-icon svg {
  height: 3.2rem;
  color: #fff;
}

#customer-icon .clu {
  color: #fff;
  font-size: 0.9rem;
}
#customer-icon:hover {
  color: chartreuse;
  fill: chartreuse;
}
@media only screen and (max-width: 767.98px) {
  #customer-icon {
    position: relative;
    margin: 0rem 2rem 0rem 0;
    top: 1rem;
  }
  #eco-icon {
    position: static;
    margin: 0rem 0 0rem 2rem;
  }
}
</style>



